.burger {
    width: 40px;
    height: 22px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;

    &__item {
        width: 40px;
        display: block;
        height: 2px;
        background: #21272D;

        &:nth-child(3) {
            width: 20px;
        }
    }
}
.mobile{
    display: none;
}
.welcome-middle,.welcome-small{
    display: none;
}
@media screen and (max-width:1250px) {
   .slider-blog{
       .slick-next{
           right: 0;
       }
       .slick-prev{
           left: 0;
       }
   }
}
@media screen and (max-width:1190px){
    .slider-one{
        .slick-prev{
            left: 0;
        }
        .slick-next{
            right: 0;
        }
        .slider__row{
            flex-direction: column;
        }
        .slider__title{
            max-width:unset;
            margin-top: 20px;
        }
        .slider__column{
            flex-basis: unset;
            text-align: center;
            align-items: center;
            &:nth-child(2){
                max-width:unset;
            }
        }
        .slider__column + .slider__column{
            margin-left: 0;
        }
    }
    .professors-slider{
        .slick-prev{
            left: 0;
        }
        .slick-next{
            right: 0;
        }
    }
    .patient__section .section__row::before{
        display: none;
    }
    .workShop__section::before{
        display: none;
    }
    .patient__section{
        .column-img{
            background: linear-gradient(180.17deg, #145793 0.11%, rgba(0, 33, 62, 0.16) 125.64%), url(/img/slider/second.png);
            background-size: cover;
        }
        .section__row{
            flex-direction: column-reverse;
        }
         .section__column:nth-child(1){
            flex-basis: unset;
        }
        .section__column:nth-child(2){
            flex-basis: unset;
            padding-top:80px;
            align-items:center;
        }
    }
    
    .img-fluid{
        position: relative;
        width: 100%;
    }
}
@media screen and (max-width:1024px){
    .welcome-big{
        display: none;
    }
    .welcome-middle{
        display: block;
    }
    .about__bckg{
        display: none;
    }
    .person-list{
        padding: 0 20px;
    }
    .professors__section::before{
        display: none;
    }
    .slider-blog__img{
        img{
            width: 100%;
        }
    }
    .slider-one__item{
        .slider__column{
            width: 100%;
        }
        .slider__img{
            img{
                width: 100%;
            }
        }
    }
    .institutes__section{
        height: auto;
        background:#145793;
        &::before{
            content:'';
            
        }
    }
    .institutes__slider{
        .img-slider{
            background: linear-gradient(180.17deg, #145793 0.11%, rgba(0, 33, 62, 0.16) 125.64%), url(/img/slider/instituteMobile.png) no-repeat;
            position: relative;
            background-size: cover;
            width: 100%;
            z-index: 2;
        }
        .slider__row{
            flex-direction: column;
        }
         .slider__column:nth-child(1){
            flex-basis: unset;
            padding:0 20px;
        }
        .slider__column:nth-child(2){
            flex-basis: unset;
            padding-top:80px;
        }
    }
}
@media screen and (max-width:900px) {
    .header {
        padding: 0 30px;
        .descktop {
            display: none;
        }

        .mobile {
            display: block;
        }
    }
    .about__bckg{
        display: none;
    }
    .main-nav{
        display: block;
    }
}
@media screen and (max-width:768px){
    .formWhom__section{
        display: unset;
    }
    .formWhom__img{
        img{
            width: 100%;
        }
    }
    .header{
        .container{
            justify-content: space-between;
        }
    }
    .slider-blog .slider-blog__item {
        margin: 0 10px;
    }
    body{
        &.overflow{
            overflow: hidden;
        }
    }
    .main-nav{
        &.active{
            transform: translateY(0);
        }
    }
    .section__row {
        flex-direction: column;
    }
    .welcome__section{
        margin-bottom: 60px;
    }
    .about__section,
    .mission__section {
        .section__column+.section__column {
            margin-left: 0;
        }
    }
    .about__section{
        .section__row{
            height: auto;
        }
    }
    .mission__section{
        .section__row{
            flex-direction: column-reverse;
        }
        .section__column{
            &.desc{
                max-width: unset;
            }
        }
    }
    .section__desc,.section__title{
        padding-right: 20px;
        padding-left: 20px;
    }
    .section__desc{
        margin-bottom: 20px;
    }
    .contacts__section{
        padding-bottom: 0;
        .section__row{
            .section__column{
                flex-basis: 100%;
                width: 100%;
            }
            .section__column + .section__column{
                margin-top: 40px;
            }
        }
        .link{
            margin-left: 20px;
        }
    }
    .workShop__section{
        .section__subTitle{
            margin-left: 20px;
        }
    }
    .img-fluid{
        margin-top: 40px;
    }
    .about__section .section__column:nth-child(1){
        max-width:unset;
    }
    .professors__section{
        padding-top: 80px;
        margin-bottom: 80px;
    }
    .certificates__section{
        margin-top: 80px;
    }
    .footer{
        height: auto;
        padding-bottom: 40px;
        .container{
            flex-flow: row wrap;
            align-content: space-between;
            padding: 0 20px;
        }
    }
    .footer-block{
        width: 50%;
        height: auto;
        margin-bottom: 20px;
        &:nth-child(3){
            order:2;
        }
        &:nth-child(2){
            order:3;
        }
        &:nth-child(3){
            order: 4;
            display: flex;
            flex-direction: column;
            align-items:flex-end;
            .footer__text{
                span{
                    display: block;
                }
            }
        }
        &:nth-child(4){
            display: flex;
            flex-direction: column;
            align-items:flex-end;
        }
    }
    .footer-list{
        flex-direction: column;
    }
    .footer-list__item + .footer-list__item{
        margin-left: 0;
        margin-top: 20px;
    }
    .forWhom-slider__item {
        .section__desc{
            padding: 0;
        }
        &.w-b{
            .forWhom-slider__img{
                display: flex;
                justify-content: center;
            }
            .forWhom-slider__info{
                justify-content: center;
                left: 50%;
                margin-left: -175px;
            }
        }
        &.no-b{
            .forWhom-slider__img{
                display: flex;
                justify-content: center;
            }
            .forWhom-slider__info{
                left: 50%;
                margin-left: -175px;
                right: unset;
            }
        }
    }
}
@media screen and (max-width:475px){
    .section__title{
        font-size: 48px;
    }
    .header__logo{
        img{
            width: 151px;
        }
    }
    .forWhom-slider__img{
        img{
            height: 500px;
        }
    }
    .forWhom-slider__item{
        &.w-b{
            &::after{
                height: 450px;
            }
            .forWhom-slider__info{
                margin-left: -150px;
            }
        }
        &.no-b{
            .forWhom-slider__info{
                margin-left: -150px;
            }
        }
        .forWhom-slider__info{
            width: 300px;
            .section__desc{
                margin-bottom: 0;
                font-size: 14px;
                margin-top: 15px;
            }
            .title-second{
                font-size: 20px;
            }
        }
    }
    .header{
        height: 100px;
    }
    .institutes__section, .certificates__section{
        .section__title{
            font-size: 38px;
        }
    }
    .professors__section{
        padding-top: 80px;
    }
    .institutes__slider{
        .img-slider{
            height: 500px;
        }
        .slider__title{
            margin-top: 60px;
        }
    }
    .welcome-middle{
        display: none;
    }
    .welcome-small{
        display: block;
    }
    .section{
        margin-bottom: 60px;
        &.institutes__section{
            margin-bottom: 0;
        }
        &.certificates__section{
            margin-top: 80px;
        }
        &.patient__section{
            .column-img{
                height: 324px;
                background: linear-gradient(180.17deg,#145793 .11%,rgba(0,33,62,.16) 125.64%),url(/img/slider/second.png) center bottom;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
        &.contacts__section{
            margin: 0;
        }
    }
    .slider-blog{
        .slider-blog__item{
            margin:0;
        }
        .slider-blog__info{
            padding:0 20px;
        }
        .slick-next,.slick-prev{
            top: 25%;
        }
        .section__desc{
            padding: 0;
        }
        .button{
            margin-left: 20px;
        }
    }
    .person-list__info{
        padding: 0;
    }
    .person-list__img{
        img{
            width: 100%;
        }
    }
    .contacts-form{
        .send-form,.input__wrapper input, .input__wrapper textarea{
            width: 300px;
        }
    }
}